import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ReporteService } from 'src/app/service/reporte.service';
import { TipoProblemaService } from 'src/app/service/tipo-problema.service';

//SWAL
declare var swal: any;

@Component({
  selector: 'app-lista-reportes',
  templateUrl: './lista-reportes.component.html'
})
export class ListaReportesComponent implements OnInit {
  filtro : any;
  p: number = 1;
  singleReporte:any =  {};
  listaReportes:any[] = [];
  listaTipoProblemas: any = [];
  
  constructor(private router: Router, 
    private reporte_service: ReporteService,
    private problema_service: TipoProblemaService) { }

  ngOnInit(): void {
    this.getAll();
    this.getTipoProblema();
  }

  getAll(): void {
    this.reporte_service.read().subscribe(
      (data) => {
        this.listaReportes = data;
        console.log(this.listaReportes)
      },
      (err) => {
        this.showNotification("Error", "No hay datos disponibles", "error");
      })
  }
  getTipoProblema(): void {
    this.problema_service.read().subscribe(
      (data) => {
        this.listaTipoProblemas = data;
      }
    )
  }

  verDatos(reporte: any) : void {
    this.singleReporte = { ...reporte };
  }


  darSeguimiento(id_reporte: number) : void{
    this.router.navigate(['reporte/', id_reporte]);
  }

  getReporteByTipo(id_tipo: number): void {
    if (id_tipo == 0) {
      this.getAll();
      return;
    }
    this.reporte_service.getReporteByProblema(id_tipo).subscribe((data) => {
      this.listaReportes = data;
      if (this.listaReportes.length == 0) {
        this.showNotification("Error", "No hay datos disponibles", "error");
      }
    }, 
    (err) => {
      this.showNotification("Error", "No hay datos disponibles", "error");
    })
  }

  showNotification(title: string, text: string, icon: string): void {
    swal({
			title: title,
			text: text,
			icon: icon,
		});
  }



}
