import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ReporteService } from 'src/app/service/reporte.service';
//SWAL
declare var swal: any;

@Component({
  selector: 'app-mis-reportes',
  templateUrl: './mis-reportes.component.html'
})
export class MisReportesComponent implements OnInit {
  //PROPIEDADES
  listaReportes: any[] = [];
  p: number = 1;
  filtro: any;
  terminados: number = 0;
  enProceso: number = 0;
  cancelados: number = 0;

  constructor(private reporte_service: ReporteService,
    private router: Router) { }

  ngOnInit(): void {
    this.getMisReportes();
  }

  getMisReportes(): void{
    this.reporte_service.getReporeteByEmpleado(+localStorage.getItem("currentId"))
    .subscribe(
      (data) => {
        this.listaReportes = data;
        if (this.listaReportes.length == 0) {
          this.showNotification("Alerta", "No hay datos disponibles", "warning");
        }
        this.listaReportes.forEach(reporte => {
          console.log(reporte)
          if (reporte.estadoId == 1 ) {
            this.cancelados++;
          } 
          else if (reporte.estadoId == 4) {
            this.terminados ++;
          } else {
            this.enProceso ++;
          }
        });  
      },
      (err) => {
        this.showNotification("Error", "Ha ocurrido un error al traer los reportes", "error");
      }
    );
  }

  darSeguimiento(id_reporte: number) : void{
    this.router.navigate(['reporte/', id_reporte]);
  }

  showNotification(title: string, text: string, icon: string): void {
    swal({
			title: title,
			text: text,
			icon: icon,
		});
  }
}
