import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LoginService } from '../../service/login.service'

//import {UsuarioService} from './../service/usuario.service'

//JQUERY
declare var $:any;
//SWAL
declare var swal:any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})

export class LoginComponent implements OnInit {


  dtincorrectos:boolean = false;

  formData:FormGroup = new FormGroup({
    'correo': new FormControl('', [Validators.required]),
    'password': new FormControl('', [Validators.required])
  });
  
  constructor(
    private router: Router,
    private loginService: LoginService
  ) { }

  ngOnInit(): void {
  }

  logIn() : void {
    this.loginService.checkCredentials(JSON.stringify(this.formData.value)).subscribe(
      (response) => {
        localStorage.setItem('currentId', response.id);
        localStorage.setItem('currentTipoUsuario', response.tipoUsuarioId);
        swal({
          title: "Bienvenido",
          text: "Sesion iniciada, bienvenido administrador.",
          icon: "success",
          type: "success",
        });
        this.goToRoute('dashboard');
      },
      (error) => {
        swal({
          title: "Error Credenciales",
          text: "El correo o contraseña son incorrectos.",
          icon: "error",
          type: "error",
        });
      }
    );    
  }

  goToRoute(strRoute:String) : void {
    this.router.navigate([strRoute]);
  }

  /**
   * ABRIR VISTA RECOVERY
   */
  openRecovery = () => {
    $("#loginform").slideUp();
    $("#recoverform").fadeIn();
  }
  
  /**
   * ABRIR VISTA INICIO SESION
   */
  iniciarSesion= () => {
    $("#loginform").fadeIn();
  }

  get correo() { return this.formData.get('correo'); }
  get password() { return this.formData.get('password'); }

}



