  
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError} from 'rxjs/operators';
import { path } from '../config.module';


@Injectable({
  providedIn: 'root'
})
export class ReporteService {

  private basePath:string = path.path;
  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    })
  };
  

constructor(private http: HttpClient) { }

  //HANDLE ERROR
  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error('Un error ha ocurrido:', error.error.message);
    } else {
      console.error(
      `Backend returned code ${error.status}, ` +
      `body was: `, error.error);
    }
    return throwError(
      error);
  };

  //READ
  public read(): Observable<any>{
    let url = `${this.basePath}reporte_sin_asignar`;
    return this.http.get(url, this.httpOptions)
    .pipe(
      catchError(this.handleError)
    )
  }

  //GET REPORTE BY Tipo Problema
  public getReporteByProblema(problema_id: number): Observable<any>{
    let url = `${this.basePath}reporte/tipo_problema/${ problema_id }`;
    return this.http.get(url, this.httpOptions)
    .pipe(
      catchError(this.handleError)
    )
  }
  //GET SINGLE
  public getSingle(reporte_id: number): Observable<any>{
    let url = `${this.basePath}reporte/${reporte_id}`;
    return this.http.get(url, this.httpOptions)
    .pipe(
      catchError(this.handleError)
    )
  }

  //GET REPORTE BY EMPLEADO
  public getReporeteByEmpleado(empleado_id: number): Observable<any>{
    let url = `${this.basePath}reporte/empleado/${empleado_id}`;
    return this.http.get(url, this.httpOptions)
    .pipe(
      catchError(this.handleError)
    )
  }

  

  //UPDATE
  public update(data:any, id_reporte: number) : Observable<any> {
    let url = `${this.basePath}reporte/${id_reporte}`;
    return this.http.put(url, data, this.httpOptions)
    .pipe(
      catchError(this.handleError)
    )
  }  
  
  
}
